class Tile {
  constructor(value, field) {
    this.value = value;
    this.tileElement = document.createElement('div');
    this.tileElement.classList.add('tile');
    field.append(this.tileElement);
    this.setValue(value);
  }

  setCoords(row, col) {
    this.row = row;
    this.col = col;

    this.tileElement.style.setProperty('--row', row);
    this.tileElement.style.setProperty('--col', col);
  }

  setValue(value) {
    this.value = value;
    this.tileElement.classList.add(`tile--${this.value}`);
    this.tileElement.textContent = this.value;
  }

  removeFromDom() {
    this.tileElement.remove();
  }

  waitForTransitionEnd() {
    return new Promise((resolve) => {
      this.tileElement.addEventListener('transitionend', resolve, {
        once: true,
      });
    });
  }
}

module.exports = Tile;
